import React, {Component} from 'react';
import Auxiliary from '../../hoc/Auxiliary';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummury from '../../components/Burger/OrderSummury/OrderSummury';
import axios from '../../axios-orders';
import Spinner from '../../components/UI/Spinner/Spinner';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/index';


class BurgerBuilder extends Component {
    state = {
        purchasing: false,
    }

    componentDidMount () {
        this.props.onInitIngredients();
    }

    updatePurchaseState (ingredients) {
        const sum = Object.keys(ingredients).map(key => {
            return ingredients[key]
        })
        .reduce( (sum, el) => {
            return sum + el;
        },0);
        return sum > 0;
    }

    purchaseHandler = () => {
        if(this.props.isAuth){
            this.setState({purchasing: !this.state.purchasing});
        } else {
            this.props.history.push({
                pathname: '/auth'
            });
        }  
    } 

    purchaseCancelhandler = () => {
        this.setState({purchasing: false});
    }

    purchaseContinuehandler = () => {  
        this.props.onPurchaseInit();
        this.props.history.push({
            pathname: '/checkout'
        });
    }
    render() {
        
        const disableInfo = {
            ...this.props.ings
        }
        for (let key in disableInfo) {
            disableInfo[key] = disableInfo[key] <= 0 
        }
        let orderSummury = null;
        let burger = this.props.error ? <p style={{textAlign:'center', marginTop:'200px', fontSize: 'x-large'}}>ingrediants cant be loaded!</p> : <Spinner/> ;

        if (this.props.ings) {
            burger = 
                <Auxiliary>
                    <Burger ingredients= {this.props.ings} />
                    <BuildControls ingredientAdded={this.props.onAddIngredient} 
                    ingredientRemoved={this.props.onRemoveIngredient} 
                    disabled={disableInfo}
                    price={this.props.price}
                    purchasble={this.updatePurchaseState(this.props.ings)}
                    purchasing={this.purchaseHandler}
                    />
                </Auxiliary>;
            orderSummury = <OrderSummury ingredients={this.props.ings} price={this.props.price}
            purchaseCancelled={this.purchaseCancelhandler} 
            purchaseContinued={this.purchaseContinuehandler} />;
        }

        return(
            <Auxiliary>
                
                <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelhandler}>
                    {orderSummury}
                </Modal>
                {burger}
            </Auxiliary>
        );
    }
}

const mapStateToProps = state =>{
    return {
        ings : state.burgerBuilder.ingredients,
        price: state.burgerBuilder.totalPrice,
        error: state.burgerBuilder.error,
        isAuth: state.auth.token !== null
    }
};
const mapDispatchToProps = dispatch => {
    return {
        onAddIngredient :(type)=> dispatch(actions.addIngrediant(type)),
        onRemoveIngredient : (type)=> dispatch(actions.removeIngrediant(type)),
        onInitIngredients :()=> dispatch(actions.initIngredients()),
        onPurchaseInit: ()=> dispatch(actions.purchaseInit())
    }
}

export default connect(mapStateToProps,mapDispatchToProps)( withErrorHandler(BurgerBuilder, axios));
